import React from 'react';
import ReactDOM from 'react-dom';

//必要样式
import './index.less';
import 'antd-mobile/dist/antd-mobile.css';

//图标样式
import './assets/style.css'

//路由相关
import routes from './router/index';
import MyRouter from "./components/myRouter/index";

//最外层只留一层router即可
import {BrowserRouter as Router} from "react-router-dom";


import eruda from "eruda";
import {getWxAuthUrl, weixinJSOAuth, getSdkConfig} from "./api/user";
import {getQueryField} from "./utils/utils";
import store from "./store";

/**
 * 启动初始化
 */
!async function () {
    console.log("初始化， 打印wx实例：");
    console.log(wx);
    eruda.init();
    await initLogin();
	await initWxConfig();
    
	//全局store测试
	let index = 1;
	setInterval(() => {
		store.dispatch({type: "message/SET_MESSAGE", playload: index ++});
		index === 99 && (index = 0);
    }, 1000);
    
    ReactDOM.render(
        <Router basename={_ENV_.ROUTER_BASENAME}>
          <MyRouter routes={routes}/>
        </Router>
    , document.getElementById('root'));

}();

/**
 * 以下是启动初始化
 */
function initWxConfig () {
    return new Promise(async (resolve, reject) => {
        const {
            config: {
                timestamp,
                signature,
                nonceStr
            }
        } = await getSdkConfig("1");//临时传1
        wx.config({
            debug: !true, 
            appId: _ENV_.APP_ID, 
            timestamp: timestamp, 
            nonceStr: nonceStr, 
            signature: signature,
            jsApiList: [
                "getLocation",
                "openLocation",
                "scanQRCode",
            ] 
        });
        wx.ready(() => resolve());
        wx.error(err => reject(err));
    });
}

/**
 * 初始化登录 
 * 先看缓存里边的user信息，有则检测是否绑定手机， 没有则去绑定手机然后返回首页。
 * 如果缓存里边没有user信息，则获取query的code与state进行微信授权并持久化存储返回的user信息，若没有code、state,则发起授权，授权后带code、state跳回进行授权处理。
 */
async function initLogin () {
    const user = store.getState().user;
    if (user)  {
        _WEBPACK_MODE_ === "development" && console.log(1);
        // 暂不强制绑定手机
        // if (!user.member) {
        //     (this as any).props.history.push("/editPhone?title=绑定手机");
        // }
        return;
    }
    const code = getQueryField("code"), state = getQueryField("state");
    if(state && code){
        _WEBPACK_MODE_ === "development" && console.log(2);
        history.replaceState(null, null, "/");
        const res: any = await weixinJSOAuth({
            state, 
            code, 
            agentId: 1
        });
        store.dispatch({type: "user/SET_USER", playload: res});
        const user = store.getState().user;
        // 暂时不强制绑定手机
        // if (!user.member) {
        //     (this as any).props.history.push("/editPhone?title=绑定手机");
        // }
    } else {
        _WEBPACK_MODE_ === "development" && console.log(3);
        const res: any = await getWxAuthUrl({
            callbackUrl: "http://ChargingBox.wx.teny.tech",
            agentId: 1
		});
        window.location.href = res.url;
    }
}