import React from "react";
import "./less/index.less";

import Topbar from "../../../../components/topbar/index";
import OrderCard from "./components/orderCard/index";
import MessageIcon from "../../../../components/topbar/components/messageIcon/index";

export default class Order extends React.Component {
    //ts才有静态属性， es只有静态方法
    static defaultProps = {

    }
    constructor (props:any) {
        super(props);
        this.state = {
            //tab切换相关状态
            tab: {
                index: 0
            },
            list: [1,2,3],
            scroll: null
        }
    }
    componentDidMount () {
        this.initMeScroll();
    }
    //启动下拉刷新，触底加载
    initMeScroll () {
        const up = () => {
            setTimeout(() => {
                // (this as any).state.scroll.endSuccess(true, !true);
                (this as any).state.scroll.endUpScroll(!false, true);
            }, 500);
        }
        const down = () => {
            setTimeout(() => {
                (this as any).state.scroll.endSuccess();
            }, 500);
        }
        (this as any).state.scroll = new MeScroll(this.refs["order-card-wrap"],  {
            up: {
                callback: up
            },
            down: {
                callback: down
            }
        });
    }

    /**
     * tab切换点击
     */
    tabTouch (index) {
        (this as any).state.tab.index = index;
        this.setState({});
    }

    test () {
        alert("测试");
    }
    render ():any {
        return (
            <div className="order">
                <div className="bar-wrap">
                    <Topbar title="订单" disableBackBtn={false} leftChild={<MessageIcon num={3}/>}/>
                </div>
                <div className="content-wrap">
                    {/* 订单类型切换 */}
                    <div className="tab">
                        <div className={"child" + ((this as any).state.tab.index === 0 ? " active" : "")} onClick={this.tabTouch.bind(this, 0)}>
                            任务订单
                            <div className="underline"></div>
                        </div>
                        <div className={"child" + ((this as any).state.tab.index === 1 ? " active" : "")} onClick={this.tabTouch.bind(this, 1)}>
                            历史订单
                            <div className="underline"></div>
                        </div>
                    </div>
                    
                    {/* 订单卡片展示容器 */}
                    <div className="order-card-wrap" ref="order-card-wrap">
                        <div className="card-in-wrap">
                        {
                            (this as any).state.list.map(item => {
                                return (
                                    <div className="card-wrap">
                                        <OrderCard/>
                                    </div>
                                )
                            })
                        }
                        </div>
                    </div>
                    
                </div>
            </div>
        );
    }
}
