import React from "react";
import "./less/index.less";

/**
 * 组件 
 */
import Topbar from "../../../../components/topbar/index";
import MessageIcon from "../../../../components/topbar/components/messageIcon/index";
import Pullup from "../../../../components/pullup/index";
import Card from "./components/card/index";
import {Toast, Modal} from "antd-mobile";
import Modal2 from "../../../../components/modal";

/**
 * 辅助方法 
 */
import { 
    getPosition,
    scanCode,
    getQueryField
} from "../../../../utils/utils";
import { qrcodeScan } from "../../../../api/home";

/**
 * 静态资源 
 */
import scan from "./icon/scan_white.png";
import topointer from "./icon/orientation.png";

/**
 * boxitem
 */
interface BoxItme {
    //坐标
    position: number[],
    //图标显示类型，这里暂定为0位选中状态， 1为正常状态， 。。。更多请看代码
    type: number,
    //当前电柜在绘制的时候生成的地图marker对象实例， 主要用作删除该柜点时候使用
    marker?: any,
    //name 自定义的测试字段
    name?: string
}

/**
 * state 
 */
interface State {
    //地图对象
    map: object,
    //地图柜点数据列表
    boxList: BoxItme[],
    //点击地图选取柜点状态数据
    touchMapCard: {show: boolean, item: BoxItme},
    //上拉框卡片列表数据
    cardList: BoxItme[],
    //我的定位点状态保存
    pointer: {marker: object, position: number[], error: boolean},
    //导航线状态数据
    line: {startMarker: object, endMarker: object, route: object},
    //接参页面跳转弹窗状态数据
    modal2: {title: string, state: string}
}

/**
 * 该页面用到的全局变量 
 */
let platform: string = "";

export default class Home extends React.Component {

    /**
     * 定位轮训定时器
     */
    public _timer = null;

    public state: State = {
        //地图对象 
        map: null,
        //附近的柜点数据
        boxList: [
            {position: [120.39172,31.49992], name: "a", type: 1},
            {position: [120.392524,31.499724], name: "b", type: 1},
            {position: [120.393619,31.498562], name: "c", type: 2},
            {position: [120.392363,31.498758], name: "d", type: 1},
            {position: [120.391768,31.500332], name: "e", type: 3},
            {position: [120.393479,31.500121], name: "f", type: 1},
            {position: [120.388356,31.498946], name: "g", type: 2},
            {position: [120.390244,31.506191], name: "h", type: 1},
            {position: [120.3887,31.505166], name: "i", type: 1},
            {position: [120.393399,31.493139], name: "j", type: 1},
            {position: [120.396017,31.503796], name: "k", type: 1},
            {position: [120.388976,31.495616], name: "l", type: 1},
            {position: [120.38962,31.496321], name: "m", type: 1}
        ],
        //点击地图上的电柜显示的卡片状态
        touchMapCard: {
            show: false,
            item: null,
        },
        //上拉框中的卡片
        cardList: [
            {position: [120.3887,31.505166], name: "i", type: 1},
            {position: [120.393399,31.493139], name: "j", type: 1},
            {position: [120.396017,31.503796], name: "k", type: 1},
            {position: [120.388976,31.495616], name: "l", type: 1},
            {position: [120.38962,31.496321], name: "m", type: 1}
        ],
        //我的定位位置
        pointer: {
            marker: null,
            position: null,
            error: null
            // position: [120.38962,31.496321] 测试用
        },
        //导航路线状态数据
        line: {
            startMarker: null,
            endMarker: null,
            route: null
        },
        //来自接参页面弹窗
        modal2: {
            title: "",
            state: ""
        }
    }

    componentDidMount () {
        this.initFromQr();
        this.initMap();
    }
    componentWillUnmount () {
        clearTimeout(this._timer);
    }

    /**
     * 初始化处理来自扫码页面跳转 
     */
    initFromQr () {
        const title = getQueryField("qrtitle"), state = getQueryField("qrstate");
        if (!title || !state) return;
        history.replaceState(null, null, "/");
        this.state.modal2.title = title;
        this.state.modal2.state = state;
        this.setState({});
    }

    /**
     * 初始化地图 
     */
    initMap (): void {
        const map: any = this.state.map = new AMap.Map(this.refs["mapContainer"], {
            zoom: 15,
            viewMode:'2D',
            mapStyle: 'amap://styles/whitesmoke',
        });
        map.on("complete", async () => {
            await new Promise((resolve, reject) => {
                map.plugin('AMap.Riding',function(){
                    resolve();
                });
            });
            (this as any).drawBoxList();
            //初次设置中心点以当前定位位置
            try {
                const position = await getPosition();
                map.setCenter(position);
            } catch(err) {
                if (err.errMsg === "getLocation:fail") {
                    this.state.pointer.error = err;
                    this.setState({});
                } else {
                    console.error(err);
                }
            }
            //开启定位轮询
            this.setPointer();
        });
    }

    /**
     * 生成导航路线 
     */
    onNavigating (item: BoxItme): void {
        // Toast.info("线路生成及导航调试中！");
        console.log(item);
        this.offTouchMapCard();
        this.drawRoute(null, item.position);
    }

    /**
     * 生成骑行导航路线 
     */
    drawRoute (startPointer: number[], endPointer: number[]) {
        const 
        map: any = this.state.map,
        riding: any = new AMap.Riding({
            map,
            panel: "panel"
        });
        if (!this.state.pointer.position) {
            Toast.info("定位中，请稍后再试...");
            return;
        }
        //根据起终点坐标规划骑行路线
        riding.search(
            startPointer ? startPointer : this.state.pointer.position, 
            endPointer, 
            (status: string, result: any): void => {
                if (status === "complete") {
                    drawRouteAction.call(this, result.routes[0]);
                } else {
                    throw status;
                }
            }
        );
        function drawRouteAction (route: object): void {
            var path = parseRouteToPath(route);
            var startMarker = this.state.line.startMarker = new AMap.Marker({
                position: path[0],
                icon: 'https://webapi.amap.com/theme/v1.3/markers/n/start.png',
                map: map
            });
            var endMarker = this.state.line.endMarker = new AMap.Marker({
                position: path[path.length - 1],
                icon: 'https://webapi.amap.com/theme/v1.3/markers/n/end.png',
                map: map
            });
            var routeLine = this.state.line.route = new AMap.Polyline({
                path: path,
                isOutline: true,
                outlineColor: '#2FDA88',
                showDir:  true,
                borderWeight: 1,
                strokeWeight: 5,
                strokeColor: '#0FC7D9',
                lineJoin: 'round'
            });
            routeLine.setMap(map);
            // 调整视野达到最佳显示区域
            map.setFitView([ startMarker, endMarker, routeLine ]);
            this.setState({});
        }
        function parseRouteToPath(route: any): any[] {
            var path = []
            for (var i = 0, l = route.rides.length; i < l; i++) {
                var step = route.rides[i]
                for (var j = 0, n = step.path.length; j < n; j++) {
                  path.push(step.path[j])
                }
            }
            return path
        }
    }

    /**
     * 导航退出点击回调 
     */
    closeLine (): void {
        const 
        line: any = this.state.line,
        map: any = this.state.map;
        line.route.setMap(null);
        map.remove(line.startMarker);
        map.remove(line.endMarker);
        line.startMarker = null;
        line.endMarker = null;
        line.route = null;
        this.setState({});
    }

    /**
     * 拉起地图导航 
     */
    toMapApp (): void {
        const 
        marker: any = this.state.line.endMarker,
        {lng, lat} = marker.getPosition();
        wx.openLocation({
            latitude: lat, // 纬度，浮点数，范围为90 ~ -90
            longitude: lng, // 经度，浮点数，范围为180 ~ -180。
            name: '电柜位置', // 位置名
            address: '右侧可以点击选择地图导航', // 地址详情说明
            scale: 10, // 地图缩放级别,整形值,范围从1~28。默认为最大
        });
    }

    /**
     * 设置自己的位置
     */
    async setPointer (position?: number[]) {
        const map: any = this.state.map, pointer = this.state.pointer;
        !position && (position = await getPosition());
        pointer.position = position;
        if (pointer.marker) {
            map.remove(pointer.marker);
        }
        const html: string = `
            <img class="pointer" src="./public/img/我的位置.png" alt=""/>
        `;
        console.log(position[0], position[1]);
        const marker: any = new AMap.Marker({
            position: new AMap.LngLat(position[0], position[1]),
            content: html,
            offset: new AMap.Pixel(-15, -15),
            zIndex: 2,
        });
        pointer.marker = marker;
        map.add(marker);
        this.setState({}, () => {
            clearTimeout(this._timer);
            (this as any)._timer = setTimeout(() => {
                this.setPointer();
            }, 5000);
        });
    }

    /**
     * 绘制地图上所有box
     * 
     * @param {Array} list
     */
    drawBoxList (list: Array<BoxItme>): void {
        if (list) (this as any).state.boxList = [...this.state.boxList, ...list];
        for (let i of this.state.boxList) {
            const marker = this.buildBox(i);
            i["marker"] = marker;
            this.drawBox(i);
        }
    }

    /**
     * 绘制单个box 
     * 
     * @param {Object} item
     */
    drawBox (item: BoxItme): void {
        const map: any = this.state.map;
        const marker: any = this.buildBox(item);
        item.marker = marker;
        if (this.getPlatform() === "pc") {
            marker.on("click", this.boxTouch.bind(this, item));
        }
        if (this.getPlatform() === "mobile") {
            marker.on("touchend", this.boxTouch.bind(this, item));
        }
        map.add(marker);
    }

    /**
     * 删除单个box
     * 
     * @param {Object} item
     */
    removeBox (item: BoxItme): void {
        const map: any = this.state.map;
        map.remove(item.marker);
    }

    /**
     * 生成box marker
     * 
     * @param {Object} item
     * @return {Object} 
     */
    buildBox (item: BoxItme): void {
        const html: string = `
            <img class="${item.type === 0 ? "boxinmap-big" : "boxinmap"}" src="./public/img/${
                item.type === 0 ? "spot-selected.png" :
                item.type === 1 ? "正常可租.png" :
                item.type === 2 ? "满仓.png" :
                item.type === 3 ? "不可用.png" : ""
            }" alt=""/>
        `;
        return new AMap.Marker({
            content: html,
            position:  item.position, // 基点位置
            offset: item.type === 0 ? new AMap.Pixel(-18.25, -45) : new AMap.Pixel(-16.25, -40), // 相对于基点的偏移位置
            zIndex: 1
        });
    }

    /**
     * 电柜点击事件 
     */
    boxTouch (item: BoxItme): void {
        if ((this as any).state.line.route) return; //处于路线导航模式时候不动作
        //如果之前有点击柜点，那么先销毁之前的点击
        if (this.state.touchMapCard.item) {
            this.offTouchMapCard();
        }
        this.removeBox(item);
        item.type = 0;
        this.drawBox(item);
        setTimeout(() => this.openTouchMapCard(item), 50);
    }

    /**
     * 回到自己的定位位置
     */
    toSelfPointer () {
        const map: any = this.state.map, position = this.state.pointer.position;
        map.setZoom(map.getZoom());
        map.setCenter(new AMap.LngLat(...position));
    }

    /**
     * 打开、关闭touchMapCard 
     */
    openTouchMapCard (item: BoxItme): void {
        this.state.touchMapCard.item = item;
        this.state.touchMapCard.show = true;
        this.setState({});
    }
    offTouchMapCard () {
        const item: BoxItme = this.state.touchMapCard.item;
        this.removeBox(item);
        item.type = 1;
        this.drawBox(item);
        this.state.touchMapCard.item = null;
        this.state.touchMapCard.show = false;
        this.setState({});
    }

    /**
     * 获取平台类型
     * 
     * @return {String} mobile|pc
     */
    getPlatform (): string {
        if (platform) return platform;
        var sUserAgent: any = navigator.userAgent.toLowerCase();
        var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
        var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
        var bIsMidp = sUserAgent.match(/midp/i) == "midp";
        var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
        var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
        var bIsAndroid = sUserAgent.match(/android/i) == "android";
        var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
        var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
        if (bIsIpad || bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM) {
            platform = "mobile";
        } else {
            platform = "pc";
        }
        return platform;
    }

    /**
     * 扫码按钮点击 
     */
    async scanCode () {
        if (this.state.touchMapCard.item) this.offTouchMapCard();
        let { resultStr } = await scanCode();
        console.log(resultStr);
        resultStr = ~resultStr.indexOf("?") ? resultStr.split("?")[1] : null;
        console.log(resultStr);
        if (!resultStr) {
            Toast.info("不是合法二维码");
            return;
        }
        const code = getQueryField("code", resultStr), action = getQueryField("action", resultStr);
        if (!code) {
            Toast.info("code缺失");
            return;
        }
        if (!action) {
            Toast.info("action缺失");
            return;
        }
        qrcodeScan({
            code,
            action
        })
        .then(err => {
            this.state.modal2.title = action === "in" ? "还电池开柜成功" : "租电池开柜成功";
            this.state.modal2.state = "success";
            this.setState({});
        })
        .catch(err => {
            this.state.modal2.title = action === "in" ? "还电池开柜失败" : "租电池开柜失败";
            this.state.modal2.state = "fail";
            this.setState({});
        });
    }

    render (): any {
        const state = this.state;
        return (
            <div className="home">

                {/* topbar部分 */}
                <div className="bar-wrap wrap-top">
                    <Topbar 
                    title="腾跃共享电池"
                    // disableBackBtn={true}
                    leftChild={<MessageIcon num={3}/>}
                    />
                </div>

                {/* 退出路线导航 */}
                {state.line.route && <div className="closeRoute-btn" onClick={this.closeLine.bind(this)}>退出</div>}
                {state.line.route && <div className="tomapapp-btn" onClick={this.toMapApp.bind(this)}>拉起地图</div>}

                {/* 跳转到自身位置按钮 */}
                <div className={"selfpointer " + (state.pointer.position ? "" : "selfpointer-move")} onClick={this.toSelfPointer.bind(this)}>
                    <img src={topointer} alt=""/>
                </div>

                {/* 扫码按钮 */}
                {!state.line.route && <div className="scancode-btn" onClick={this.scanCode.bind(this)}>
                    <div>
                        <img src={scan} alt=""/>
                        <div className="text">租电</div>
                    </div>
                </div>}

                {/* 点击地图电柜显示信息卡片 */}
                {
                state.touchMapCard.show &&
                <div className="touchmapcard-wrap" onClick={e => {e.stopPropagation()}}>
                    <Card item={state.touchMapCard.item} onClose={this.offTouchMapCard.bind(this)} onNavigating={this.onNavigating.bind(this)}/>
                </div>
                }

                {/* 地图容器 */}
                <div className="map-container" ref="mapContainer"></div>

                {/* 上拉组抽屉件 */}
                {!state.line.route && <Pullup title="附近电柜" tips="上拉查看周边柜点详情" topSpacing={57}>
                    <div className="pullup-inwrap">
                        <div className="search">
                            <div className="icon icon-custom-sousuo"></div>
                            <input type="text" placeholder="搜索关键字，如城市、小区、道路等"/>
                        </div>
                        {
                        this.state.cardList.map(item => {
                            return (
                                <div className="card-wrap">
                                    <Card disableCloseBtn={true} item={item} onNavigating={this.onNavigating.bind(this)}/>
                                </div>
                            )
                        })
                        }
                    </div>
                </Pullup>}

                {/* 定位失败确认弹窗 */}
                <Modal
                visible={state.pointer.error}
                transparent
                maskClosable={false}
                title="定位失败"
                footer={[{ text: '我知道了', onPress: () => { state.pointer.error = null; this.setState({})} }]}
                >
                    <p>请开启手机定位。</p>
                </Modal>

                {/* 扫码状态模态框 */}
                {state.modal2.title && <Modal2 ref="modal2" text={state.modal2.title} confirm_text="我知道了"  description="" img_name={state.modal2.state ==="success" ? "success" : "fail"} 
                onConfirm={() => {(this as any).refs.modal2.handleClose(), state.modal2.title = "", state.modal2.state = "", this.setState({})}}
                />}
                        
            </div>
        );
    }
}