export default class Message extends React.Component {
	
	constructor (props) {
		super(props);
		this.state = {
			scroll: null,
			tab: {
				index: 0
			},
			list: [],
		}
	}
	componentDidMount () {
		const _this:any = this;
		//初始化下拉加载
		(this as any).state.scroll = new MeScroll(this.refs["content-wrap"], {
			up: {
				auto: false,
				callback () {
					setTimeout(() => {
						_this.state.scroll.endUpScroll(true);
					}, 1000);
				}
			},
			down: {
				callback () {
					setTimeout(() => {
						_this.state.scroll.endSuccess();
					}, 1000);
				}
			}
		});
		setTimeout(() => {
		(this as any).state.scroll.showDownScroll();
		setTimeout(() => {
			this.setState({list: list1});
			(this as any).state.scroll.endDownScroll();
		}, 500);
		}, 100);
	}

	/**
     * tab切换点击
     */
    tabTouch (index) {
		(this as any).state.tab.index = index;
		switch (index) {
			case 0:
				(this as any).state.list = list1;
			break;
			case 1:
				(this as any).state.list = list2;
		}
		(this as any).state.scroll.showDownScroll();
		setTimeout(() => {
			this.setState({});
			(this as any).state.scroll.endDownScroll();
		}, 200);
    }

	render (): any {
		return (
			<div className="message">
				<div className="top-wrap">
					<Topbar title="消息中心"/>
				</div>

				{/* 类型切换 */}
				<div className="tab">
					<div className={"child" + ((this as any).state.tab.index === 0 ? " active" : "")} onClick={this.tabTouch.bind(this, 0)}>
						活动资讯
						<div className="underline"></div>
					</div>
					<div className={"child" + ((this as any).state.tab.index === 1 ? " active" : "")} onClick={this.tabTouch.bind(this, 1)}>
						系统消息
						<div className="underline"></div>
					</div>
				</div>

				<div className="content-wrap" ref="content-wrap">
					<div>
					{
						(this as any).state.list.map(item => {
							if ((this as any).state.tab.index === 0) {
								return (
									<div className="card">
										<img src={item.img} alt=""/>
										<div className="text text1">{item.title}</div>
										<div className="text text2">{item.summary}</div>
									</div>
								);
							}
							if ((this as any).state.tab.index === 1) {
								return (
									<div>
										{/* 日期显示胶囊 */}
										<div className="date-capsule-wrap">
											<div className="date-capsule">{item.date}</div>
										</div>
										{/* 日期信息显示 */}
										{
											item.list.map(item => {
												return (
													<div className="text-card">
														<div className="title">{item.title}</div>
														<div className="content">{item.content}</div>
														<div className="summary">{item.summary}</div>
													</div>
												);
											})
										}
									</div>
								);
							}
						})
					}
					</div>
				</div>
			</div>
		);
	}
}