import React from "react";
import "./less/index.less";

import Topbar from "../../components/topbar/index";
import LeftTimeBtn from "../../components/leftTimeBtn";
import store from "../../store";
import { getQueryField } from "../../utils/utils";
import {sendCaptcha, bindPhone} from "../../api/user";
import { Toast } from "antd-mobile";

export default class EditPhone extends React.Component {

    state = {
        title: "修改手机",
        type: "edit", //add edit
        bindPhone: "",
        phone: "",
        captcha: "",
        vToken: "",
    }

    input (target, {target: {value}}) {
        this.state[target] = value;
        this.setState({});
    }

    componentDidMount () {
        const title = getQueryField("title");
        if (title) this.state.type = "add";
        this.setState({});
        //新增
        if (this.state.type === "add") {
            this.setState({title});
            document.title = title;
        }
        //编辑
        if (this.state.type === "edit") {

        }
    }

    /**
     * 发送验证码 
     */
    sendCaptcha (reset) {
        const state = this.state;
        if (!state.phone) {
            Toast.info("请先输入手机号");
            reset();
            return;
        }
        const data = {
            "agentId": 1,
            "phone": state.phone,
            "oauthToken": store.getState().user.token
        }
        console.log(data);
        sendCaptcha(data)
        .then(res => {
            state.vToken = res.token;
        })
        .catch(err => reset());
    }

    /**
     * 绑定手机 
     */
    bindPhone () {
        const 
        state = this.state,
        data = {
            "agentId": 1,// 代理ID
            "phone": state.phone,
            "vcodeToken": state.vToken,
            "vcode": state.captcha,
            "oauthToken": store.getState().user.token,
        };
        console.log(data);
        bindPhone(data)
        .then(res => {
            store.dispatch({type: "user/SET_USER", playload: res});
            (this as any).props.history.goBack();
        })
        .catch(err => {});
    }

    render (): any {
        const state = this.state;
        return (
            <div className="editnickname-wrap">
                <div className="top-wrap">
                    <Topbar title={state.title} disableBackBtn={state.type === "add"}/>
                </div>
                <div className="content-wrap">

                    {/* 已绑定手机显示 */}
                    {state.bindPhone && <div className="phone-number-block">
                        <div className="text">您当前的手机号为</div>
                        <div className="text">{state.bindPhone}</div>
                    </div>}

                    <div className="input-row">
                        <input type="text" placeholder="请输入新的手机号" value={state.phone} onChange={this.input.bind(this, "phone")}/>
                    </div>
                    <div className="input-row">
                        <input type="text" placeholder="请输入收到的手机验证码" value={state.captcha} onChange={this.input.bind(this, "captcha")}/>
                        <div className="send-captch-wrap">
                            <LeftTimeBtn onClick={this.sendCaptcha.bind(this)}/>
                        </div>
                    </div>

                    <div 
                    className={"submit-btn" + (state.phone.length > 0 && state.captcha.length > 0? " submit-btn_active" : "")}
                    onClick={ state.phone.length > 0 && state.captcha.length > 0 ? this.bindPhone.bind(this) : () => {}}
                    >确定修改</div>
                </div>
            </div>
        );
    }
}