import React from "react";
import "./less/index.less";

/**
 * 静态资源 
 */
import navigation from "./icon/navigation.png";
import loading from "./icon/loading.svg";

export default class Card extends React.Component {

    constructor (props) {
        super(props);
    }

    static defaultProps = {
        item: {},
        disableCloseBtn: false,
        onClose: null,
        onNavigating: null
    }

    state = {
        loading: true,
    }

    componentDidMount () {
        setTimeout(() => {
            this.setState({loading: false});
        }, 500 + (Math.random() * 1000));
    }

    render () {
        const 
        props: any = this.props,
        state = this.state;
        return (
            <div className="home-card">
                <div className="close-icon icon-custom-cha" style={{display: props.disableCloseBtn ? "none" : "block"}} onClick={e => props.onClose && props.onClose()}></div>
                <div className="row">
                    <div className="title">无锡科创园B区充电柜</div>
                    <div className="capsule capsule-green">可接</div>
                    <div className="capsule capsule-red">可接</div>
                </div>
                <div className="row" style={{justifyContent: "space-between"}}>
                    <div className="small-text">无锡市新区长江北路无锡科创园B栋</div>
                    <img className="pointer" src={navigation} alt="" 
                    onClick={e => {
                        e.stopPropagation();
                        props.onNavigating && props.onNavigating(props.item);
                    }}
                    />
                </div>
                <div className="row" style={{justifyContent: "space-between"}}>
                    <div>
                        <div className="text">收费：</div>
                        <div className="title" style={{color: "#0FC7D9"}}>3元/小时&nbsp;</div>
                        <div className="mini-text">(包月：0.5元/小时）</div>
                    </div>
                    <div className="mini-text" style={{alignSelf: "flex-end"}}>1200米</div>
                </div>
                <div className="row" style={{justifyContent: "space-around"}}>
                    <div className="row-child">
                        <div className="title">21</div>
                        <div className="mini-text">电池储备</div>
                    </div>
                    <div className="row-child">
                        <div className="title">21</div>
                        <div className="mini-text">电池储备</div>
                    </div>
                    <div className="row-child">
                        <div className="title">21</div>
                        <div className="mini-text">电池储备</div>
                    </div>
                </div>
                {/* 临时loading */}
                {state.loading && <div className="loading">
                    <img className="embed" src={loading} alt=""/>
                </div>}
            </div>
        );
    }
}