import React, {Component} from 'react';
import "./less/index"
import { List, InputItem } from 'antd-mobile';

import Topbar from "../../components/topbar/index";

export default class Recharge extends Component {
    constructor(props){
        super(props);
        this.state = {
        }
    }
    render () {
        return (
            <div>
                <Topbar title="余额充值"/>
                <div className="recharge_content">
                    {/* 选择充值套餐 */}
                    <div>选择充值套餐：</div>
                    {/* 金额 */}
                    <div className="price_wrapper">
                        <div>10元</div>
                        <div>20元</div>
                        <div>50元</div>
                    </div>
                    <div className="price_wrapper">
                        <div>100元</div>
                        <div>200元</div>
                        <div>500元</div>
                    </div>
                    <List className="marTop12">
                        <InputItem
                            placeholder="0.00"
                            extra="¥"
                        >其他金额:</InputItem>
                    </List>
                    {/* 确认充值 */}
                    <div className="btn">确认充值</div>
                </div>
            </div>
        )
    }
}