import React from "react";
import "./less/index.less";
import loading_svg from "./icon/loading.svg";
import error from "./icon/error.jpg";

interface Props {
    style?: object,
    src?: string,
    onLoad?(): void,
    onError?(): void
}

export default class loadImg extends React.Component {
    static defaultProps: Props = {
        style: {},
        src: ""
    }
    state = {
        status: "loading", //loading\success\error
    }
    constructor (props) {
        super(props);
    }

    componentDidMount () {
        (this as any).refs["img"].onload = e => {
            this.setState({status: "success"});
            (this as any).props.onLoad && (this as any).props.onLoad();
        }
        (this as any).refs["img"].onerror = e => {
            this.setState({status: "error"});
            (this as any).props.onError && (this as any).props.onError();
        }
    }

    render (): any {
        const state = this.state;
        return (
            <div className="loadimg-wrap">
                {state.status === "loading" && <img className="embed" src={loading_svg}/>}
                {state.status !== "error" && <img style={{display: state.status === "success" ? "block" : "none"}} src={(this as any).props.src} ref="img"/>}
                {state.status === "error" && <img className="loadfail" src={error}/>}
            </div>
        );
    }
}