import React from "react";
import "./less/index.less";
import Fail from "../../assets/img/fail.png";
import Checking from "../../assets/img/checking.png";
import Empty from "../../assets/img/empty.png";
import Success from "../../assets/img/success.png";
// animate
import "animate.css";
import ReactCSSTransitionGroup from "react-addons-css-transition-group";

export default class Modal extends React.Component {
    static defaultProps = {
        //类型：alert,confirm,loading
        type: "alert",
        //类型success,fail
        status:"success",
        //图片名checking,empty,fail,success
        img_name:"success",
        //标题
        title:"",
        //状态文字
        text:"状态文字",
        //描述文字
        description:"描述文字",
        //确定按钮
        confirm_text:"确定",
        //取消按钮
        cancel_text:"取消",
        //禁用点击
        disableMaskTouch: true,
        //取消方法
        onCancel:()=>{},
        //确认方法
        onConfirm:()=>{
        }
    }
    constructor (props: any) {
        super(props);
        this.state = {
            mask_show: true,
            delay_mask_show:true
        };
    }
    componentDidMount () {
    }
    parseImgPath = (name)=> {
        if(name == 'success')return Success
        if(name == 'fail')return Fail
        if(name == 'empty')return Empty
        if(name == 'checking')return Checking
    }
    handleCancel = () => {
        (this as any).setState({
            mask_show: false
        });  
        (this as any).props.onCancel()
    }
    handleConfirm = () => {
        (this as any).props.onConfirm()
    }
    handleClose = (event) => {
        if ((this as any).props.disableMaskTouch) return;
        if(event.target.parentNode.getAttribute('data-type')){
            setTimeout(()=>{
                (this as any).setState({
                    delay_mask_show: false
                })  
            },500);
            (this as any).setState({
                mask_show: false
            })  
        }
    }
    render (): any {
        return (
            <div className="modal" data-type="mask" onClick={(this as any).handleClose} style={{display:(this as any).state.delay_mask_show?'fixed':'none'}}>
                {/* 模态框 */}
                <div className="modal_mask">
                    <ReactCSSTransitionGroup
                        transitionEnter={true}
                        transitionLeave={true}
                        transitionEnterTimeout={100}
                        transitionLeaveTimeout={100}
                        transitionName="animated"
                    >
                        <div  key="amache" className="animated bounceIn modal_wrapper" style={{left:(this as any).state.mask_show?'50%':'-50%'}}>
                            {/* 标题 */}
                            <div className="title" style={{display:(this as any).props.type=="slot"?'none':'block'}}>{(this as any).props.title}</div>
                            {/*  图片 */}
                            <img src={(this as any).parseImgPath((this as any).props.img_name)} style={{display:(this as any).props.type=="slot"?'none':'block'}}/>
                            {/* 状态文字 */}
                            <div className="text" style={{display:(this as any).props.type=="slot"?'none':'block'}}>{(this as any).props.text}</div>
                            {/* 描述文字 */}
                            <div className="description_text" style={{display:(this as any).props.type=="slot"?'none':'block'}}>{(this as any).props.description}</div>
                            {/* 插槽区域 */}
                            <div style={{display:(this as any).props.type=="slot"?'block':'none'}}>{this.props.children}</div>
                            {/* 按钮区域 */}
                            <div className="bth_wrapper" style={{display:(this as any).props.type == 'confirm'?'flex':'none'}}>
                                <div className="cancel" onClick={(this as any).handleCancel}>{(this as any).props.cancel_text}</div>
                                <div className="confirm" onClick={(this as any).handleConfirm}>{(this as any).props.confirm_text}</div>
                            </div>
                            <div className="bth_wrapper" style={{display:(this as any).props.type == 'slot'?'flex':'none'}}>
                                <div className="cancel" onClick={(this as any).handleCancel}>{(this as any).props.cancel_text}</div>
                                <div className="confirm" onClick={(this as any).handleConfirm}>{(this as any).props.confirm_text}</div>
                            </div>
                            <div className="bth_wrapper" style={{display:(this as any).props.type == 'alert'?'flex':'none'}}>
                                <div className="alert_confirm" onClick={(this as any).handleConfirm}>{(this as any).props.confirm_text}</div>
                            </div>
                        </div>

                    </ReactCSSTransitionGroup>
                </div>
            </div>
        );
    }
}