import React, {Component} from "react";
import "./less/index";

import color_bg from "./icon/card_color.png";
import grey_bg from "./icon/card_gray.png";
import spot from "./icon/spot.png";
import discount from "./icon/discount.png";
import insurance from "./icon/insurance.png";

import {Button, WhiteSpace, List, Modal, Toast } from "antd-mobile";
import Topbar from "../../components/topbar/index";

const alert = Modal.alert;
const showAlert = () => {
    const alertInstance = alert('温馨提示', '你确定要退还押金吗？<br/>1111', [
      { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
      { text: '继续退还', onPress: () => console.log('ok') },
    ]);
    setTimeout(() => {
      // 可以调用close方法以在外部close
      console.log('auto close');
      alertInstance.close();
    }, 500000);
  };
export default class RefundCash extends Component {
    constructor (props) {
        super(props);
        this.state = {
            //是否缴纳押金
            hasPrice: !true,
        }
    }
    /**
     * 退还押金
     */
    backPrice () {
        Toast.info("退还押金逻辑");
        this.setState({hasPrice: false});
    }
    /**
     * 缴纳押金 
     */
    payPrice () {
        Toast.info("缴纳押金逻辑");
        this.setState({hasPrice: true});
    }
    render () {
        return (
            <div className="refundcash-wrap">

                <div className="top-wrap">
                    <Topbar title="押金抵退"/>
                </div>

                <div className="content-wrap">

                    <div className="top-card">
                        <div className="top-card-inwrap">
                            <div className="circle">
                                {/* <div className="bg"></div> */}
                                {(this as any).state.hasPrice ? <img className="bg" src={color_bg} alt=""/> : <img className="bg" src={grey_bg} alt=""/>}
                            </div>
                            <div className="text-wrap">
                                <div className="row">
                                    <p className="text">押金金额</p>
                                </div>
                                <div className="row">
                                <span className="text">￥</span><span className="price-text">180.00</span><span className="text">元</span>
                                </div>
                                <div className="row">
                                    {(this as any).state.hasPrice ? <p className="text">已为您贴心护航 187 天</p> : <p className="text">认证交付押金后即可无障碍租电</p>}
                                </div>
                            </div>
                        </div>
                        {(this as any).state.hasPrice ? <div className="btn" onClick={this.backPrice.bind(this)}>退还押金</div> : <div className="btn btn-active" onClick={this.payPrice.bind(this)}>支付押金</div>}
                    </div>

                    <div className="tips">
                         <div className="text">
                            <span className="iconfont icon-custom-cuowu" style={{color: "orange"}}></span>&nbsp;
                            押金退还后，您将无法继续享受租电服务
                        </div>
                    </div>

                    <div className="card">
                        <div className="left">
                            <img style={{width: "33px", height: "45px"}} src={spot} alt=""/>
                        </div>
                        <div className="right">
                            <div className="text1">全国6000个网点自由租电</div>
                            <div className="text2">无论身在何处，我们都会在您举步维艰的时候为您提供前进的动力</div>
                        </div>
                    </div>
                    <div className="card">
                        <div className="left">
                            <img style={{width: "40px", height: "40px"}} src={discount} alt=""/>
                        </div>
                        <div className="right">
                            <div className="text1">优先享受各类优惠折扣活动</div>
                            <div className="text2">无论是租电服务或电子商城，您都将将优先享受更优惠的价格与名额</div>
                        </div>
                    </div>
                    <div className="card">
                        <div className="left">
                            <img style={{width: "33px", height: "42.5px"}} src={insurance} alt=""/>
                        </div>
                        <div className="right">
                            <div className="text1">额外的人身安全保障服务</div>
                            <div className="text2">即使已经拥有出类拔萃的安全技术保障，我们依旧为每一位用户购置了安全保险，为您的出行保驾护航</div>
                        </div>
                    </div>

                </div>

            </div>
        );
    }
}    