import React from "react";
import { useState, useEffect } from 'react';

export function Test() {
  const [count, setCount] = useState(0);
  useEffect(()=>{
    alert(1)
  },[])
  useEffect(()=>{
    alert(2)
  })
  return (
    <>
      Count: {count}
      <button onClick={() => setCount(count + 1)}>+</button>
      <button onClick={() => setCount(count - 1)}>-</button>
    </>
  );
}