import React from "react";
import "./less/index.less";
import {withRouter} from "react-router-dom";

class Topbar extends React.Component {
    //！！！es6里边只有静态方法，没有静态属性，这里静态属性是ts的语法。
    static defaultProps = {
        //背景颜色
        background: null,
        //左侧插槽
        leftChild: null,
        //禁用返回按钮插槽（默认会显示返回，在主页等页面的时候需要禁用让其不显示出来）
        disableBackBtn: false,
        //标题
        title: "",
        //返回按钮点击
        onBack: null
    }
    constructor (props: any) {
        super(props);
    }
    /**
     * 处理返回点击, 在返回按钮显示时候才生效，用户自定义leftChild时候无效
     * 判断onBack回掉有的时候就执行该毁掉，反之执行路由返回
     */
    handleBackClick () {
        if ((this as any).props.onBack) {
            (this as any).props.onBack();
        } else {
            (this as any).props.history.goBack();
        }
    }
    render (): any {
        return (
            <div className="topbar" style={{background: (this as any).props.background || ""}}>
                {/* 左侧插槽*/}
                <div className="child">
                    {
                        !(this as any).props.disableBackBtn ? 
                        (this as any).props.leftChild ? 
                        (this as any).props.leftChild :
                        <span className="iconfont icon-custom-fanhui" onClick={(this as any).handleBackClick.bind(this)}></span> :
                        ""
                    }
                </div>
                {/* 标题 */}
                <div className="child title">{(this as any).props.title}</div>
                {/* 右侧暂时为空 */}
                <div className="child right"></div>
            </div>
        );
    }
}

export default withRouter(Topbar);