import React from  "react";
import "./less/index.less";
import {withRouter} from "react-router-dom";

/**
 * tabbar菜单都在这里配置 
 */
const tabs: any = [
    {
        text: "首页",
        path: "/",
        //扩展更多...
    },
    {
        text: "订单",
        path: "/order",
        //扩展更多...
    },
    {
        text: "我的",
        path: "/person",
        //扩展更多...
    }
]

import store from "../../store";

class Tabbar extends React.Component {
    constructor (props:any) {
        super(props);
        this.state = {
            tabs
        }
        this.getMessage();
        store.subscribe(this.getMessage.bind(this));
    }
    componentDidMount () {
        this.initActiveTab((this as any).props.location.pathname);
        (this as any).props.history.listen(({pathname}) => {this.initActiveTab(pathname)});
    }

    getMessage () {
        const message = store.getState().message;
        (this as any).state.tabs.forEach(item => {
            item.num = message;
        });
        this.setState({});
    }

    /**
     * 初始化激活tab 
     * 
     * @param {String} path
     */
    initActiveTab (path) {
        if (!path) return;
        const item = (this as any).state.tabs.find(item => item.path === path);
        if (!item) return;
        (this as any).state.tabs.forEach(item => item.active = false);
        item.active = true;
        this.setState({});
    }

    /**
     * tab点击处理
     * 
     * @param {Object} item 当前点击对应的tab对象
     */
    touch (item) {
        this.initActiveTab(item.path);
        (this as any).props.history.replace(item.path);
    }

    render ():any {
        return (
            <div className="tabbar">
                {
                    (this as any).state.tabs.map(item => {
                        return (
                            <div className={'child' +  (item.active ? ' active' : '')} onTouchStart={(this as any).touch.bind((this as any), item)}>
                                {item.text}
                                {item.num > 0 && <div className="pointer">{item.num}</div>}
                                <div className="underline"></div>
                            </div>
                        );
                    })
                }
            </div>
        );
    }
}
export default withRouter(Tabbar);