import React, {Component} from "react";
import { List, TextareaItem, InputItem, Button, WhiteSpace } from 'antd-mobile';
import { ImagePicker } from 'antd-mobile';
import "./less/index"
const data = [{
    url: 'https://zos.alipayobjects.com/rmsportal/PZUUCKTRIHWiZSY.jpeg',
    id: '2121',
  }, {
    url: 'https://zos.alipayobjects.com/rmsportal/hqQWgTXdrlmVVYi.jpeg',
    id: '2122',
  }];

import Topbar from "../../components/topbar/index";
import Uploader from "../../components/uploader/index";

export default class Feedback extends Component {

    public state = {
        textarea: "",
        imgUploadMaxCount: 5,
        files: []
    };

    constructor (props) {
        super(props);
    }

    input (target, e) {
        if (target === "textarea" && e.target.value.length > this.state.textarea.length && (this.state.textarea.length >= 150)) {
            this.setState({textarea: this.state.textarea.slice(0, 150)});
            return;
        }
        this.state[target] = e.target.value;
        this.setState({});
    }

    //图片上传逻辑
    uploadAction (file, next) {
        const fr = new FileReader();
        fr.onload = function () {
            next(fr.result);
        }
        fr.readAsDataURL(file);
    }   

    //图片选择器变动监听
    fileChange (list: any) {
        console.log("图片上传变动");
        console.log(list);
    }

    render () {
        return (
            <div className="feedback_content">

                <div className="top-wrap">
                    <Topbar title="意见反馈"/>
                </div>
                <div className="content-wrap">

                    {/* 问题描述 */}
                    <div className="card">
                        <div className="title">问题描述</div>
                        <textarea 
                        className="textarea" 
                        placeholder="请输入您遇到的意见或建议"
                        value={this.state.textarea}
                        onChange={this.input.bind(this, "textarea")}
                        />
                        <div className="text-size-capsule">{this.state.textarea.length}/100</div>
                    </div>

                    {/* 图片上传 */}
                    <div className="card">
                        <div className="title">上传问题图片或截图(0/{this.state.imgUploadMaxCount})</div>
                        <div className="imgupload-wrap">
                            <Uploader 
                            files={this.state.files}
                            length={this.state.imgUploadMaxCount}
                            uploadAction={this.uploadAction.bind(this)}
                            onChange={this.fileChange.bind(this)}
                            />
                        </div>
                    </div>

                    {/* 联系方式表单 */}
                    <div className="card">
                        <div className="title">您的联系方式</div>
                        <div className="input-wrap">
                            <div className="row">
                                <div className="label">*您的姓名：</div>
                                <input type="text" placeholder="怎么称呼您？"/>
                            </div>
                            <div className="row">
                                <div className="label">*联系电话：</div>
                                <input type="text" placeholder="请填写您的电话号码"/>
                            </div>
                            <div className="tips">*工作人员将尽快与您取得联系，帮您解决遇到的问题。</div>
                        </div>
                    </div>

                    <div className="btn">提交</div>

                    <div className="bottom-tips "><span className="icon-custom-lianxi" style={{color: "green"}}></span> 您也可以选择拨打客服电话：400-888-8888</div>

                </div>
            </div>
        );
    }
}    