import React from "react";
import "./less/index.less";

export default class LeftTimeBtn extends React.Component {

    static defaultProps = {
        onClick: null
    }

    state = {
        leftTime: 0,
        timer: null
    }

    sendAction () {
        if ((this as any).state.leftTime !== 0) return;
        this.leftTime();
        if ((this as any).props.onClick) {
            (this as any).props.onClick(() => this.resetTime());
        } else {
            console.error("当前没有传递onClick回调；可以在onClick回调里边执行参数函数reset()来中断倒计时");
        }
    }

    resetTime () {
        clearInterval(this.state.timer);
        this.setState({leftTime: 0});
    }

    leftTime () {
        this.setState({leftTime: 60});
        this.state.timer = setInterval(() => {
            if (this.state.leftTime === 0) {
                clearInterval(this.state.timer);
                return;
            }
            this.state.leftTime --;
            this.setState({});
        }, 1000);
    }

    render (): any {
        const state = this.state;
        return (
            <div className="lefttimebtn-wrap">
                {state.leftTime === 0 && <div className="send-btn" onClick={(this as any).sendAction.bind(this)}>发送验证码</div>}
                {state.leftTime > 0 && <div className="text">发送验证码({state.leftTime})s</div>}
            </div>
        );
    }
}