import React from "react";
import "./less/index.less";

import Topbar from "../../components/topbar/index";

export default class EditNickname extends React.Component {

    state = {
        nickname: ""
    }

    input (target, {target: {value}}) {
        this.state[target] = value;
        this.setState({});
    }

    render (): any {
        const state = this.state;
        return (
            <div className="editnickname-wrap">
                <div className="top-wrap">
                    <Topbar title="修改昵称"/>
                </div>
                <div className="content-wrap">
                    <div className="input-row">
                        <input type="text" placeholder="请输入新的昵称" value={state.nickname} onChange={this.input.bind(this, "nickname")}/>
                    </div>
                    <div className={"submit-btn" + (state.nickname.length > 0 ? " submit-btn_active" : "")}>确定修改</div>
                </div>
            </div>
        );
    }
}