import React from "react";
import "./less/index.less";

import Topbar from "../../components/topbar/index";

/**
 * 图片资源 
 */
import yajin from "./icon/押金.png";
import more from "./icon/more.png";

export default class Wallet extends React.Component {

    constructor (props) {
        super(props);
    }

    toPage (path: string): void {
        (this as any).props.history.push(path);
    }

    render (): any {
        return (
            <div className="wallet-wrap">
                <div className="top-wrap">
                    <Topbar title="我的钱包" background="none"/>
                </div>
                <div className="header-wrap">
                    <div className="header-card">
                        <div className="header-in-card">
                            <div className="text1">余额(元）</div>
                            <div className="text2">30.00</div>
                            <div className="text3">余额付款享受更多优惠</div>
                        </div>
                        <div className="deal-detail" onClick={this.toPage.bind(this, "/TransactionList")}>交易明细</div>
                    </div>
                    <div className="pay-btn icon-custom-money" onClick={this.toPage.bind(this, "/personRecharge")}> 充值</div>
                </div>

                <div className="menu-list-wrap">
                    <div className="menu-list">
                        <div className="item" onClick={this.toPage.bind(this, "/personRefundCash")}>
                            <div className="left">
                                <img src={yajin} alt=""/>
                            </div>
                            <div className="center">押金抵退</div>
                            <div className="blance blance-active">30.00</div>
                            <img className="right" src={more} alt=""/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}