import React, {Component} from 'react';
import Certified from './img/certified.png';
import Border from './img/border.png';
import "./less/index"

import Topbar from "../../components/topbar/index";

export default class Identify extends Component {
    constructor(props){
        super(props);
        this.state = {
            //是否认证
            certified: false,
        }
    }

    toPage (path) {
        if (!path) return;
        (this as any).props.history.push(path);
    }

    render () {
        return (
            <div className="identify_content">
                <Topbar title="实名认证"/>
                <div className="wx_header_wrapper">
                    <img className="bk" src={Border}/>
                    <img className="header_img" src={Certified}/>
                </div>
                <img className="certified" style={{"display":(this as any).state.certified?'block':'none'}} src={Certified}/>
                <div className="description_title">{(this as any).state.certified?'信息已认证':'请完善您的个人信息'}</div>
                <div className="description_content">{(this as any).state.certified?'认证信息仅用于腾跃共享充电用户安全服务，您的隐私将被严格妥善保管！':'为遵守国家《互联网安全使用条例》，同时保障用户安全，我们为每一位用户购置了人身保险，需要您配合完成实名认证，您的隐私信息将被严格妥善保管。'}</div>
                <div className="btn" style={{"display":(this as any).state.certified?'none':'block'}} onClick={this.toPage.bind(this, "/realName")}>实名认证</div>
                <div className="btn_description" style={{"display":(this as any).state.certified?'none':'block'}}>仅需要2分钟</div>
            </div>
        )
    }

}