import React from "react";
import "./less/index.less";

import {Toast} from "antd-mobile";

import loading_svg from "./icon/loading.svg";

/**
 * props  
 */
interface Props {
    //文件列表
    files?: Array<{url: string}>,
    //照片最大张数
    length?: number,
    //change事件
    onChange?(list: any): void
    //upload执行函数
    uploadAction?(file: File, next: any): void
}

export default class Uploader extends React.Component {
    static defaultProps: Props = {
        files: [],
        length: 0
    }
    public state = {
        list: []
    }
    constructor (props) {
        super(props);
    }
    componentDidMount () {
        this.initUpload();
        this.formatDisplayList((this as any).props.files);
    }

    /**
     * 初始化文件上传 
     */
    initUpload () {
        const fileInput = (this as any).refs["file-input"];
        const next = (url: string): any => {
            this.setState({loading: false});
            if (!url) return;
            const list = this.formatOutList();
            list.push({url});
            this.formatDisplayList(list);
            this.trigger("onChange", list);
        }
        fileInput.onchange = () => {
            if (fileInput.files.length === 0) return;
            const file = fileInput.files[0];
            fileInput.value = "";
            this.setState({loading: true});
            this.trigger("uploadAction", file, (url: string): void => {next(url)});
        }
    }

    /**
     * 初始化传入的一维files 为显示需要的list
     */
    formatDisplayList (arr: Array<{url: string}>) {
        const list: any = [];
        arr = arr.filter(item => item.url);
        if (arr.length < (this as any).props.length || (this as any).props.length === 0) arr.push({url: null});
        for (let i = 0; i < arr.length; i += 3) {
            list.push(arr.slice(i, i + 3));
        }
        this.setState({list});
    }

    /**
     * 格式化显示list为一维list 
     * 
     * @return {Array} list
     */
    formatOutList () {
        const list: any = [];
        this.state.list.forEach(item => {
            item.forEach(item => {
                item.url && list.push(item);
            });
        });
        return list;
    }

    /**
     * 添加点击 
     */
    add () {
        (this as any).refs["file-input"].click();
    }

    /**
     * 删除按钮点击 
     * 
     * @param {Number} index
     */
    del (col, row) {
        this.state.list[col].splice(row, 1);
        this.formatDisplayList((this as any).formatOutList(this.state.list));
        this.trigger("onChange", this.formatOutList());
    }

    /**
     * 触发changge事件 
     * 
     * @param {String} type
     */
    trigger (type, ...args) {
        (this as any).props[type] && (this as any).props[type](...args);
    }

    render (): any {
        return (
            <div className="uploader">
                {
                    this.state.list && this.state.list.map((item, index) => {
                        return (
                            <div className="item">

                                {/* 已选择文件显示 */}
                                {
                                    item.map((item1, index1) => {
                                        if (item1.url === null) {
                                            return (
                                                <div className="child">
                                                    <div className="child-inwrap">
                                                        <div className="add icon-custom-jia2" onClick={this.add.bind(this)}></div>
                                                    </div>
                                                </div>
                                            );
                                        } else {
                                            return (
                                                <div className="child">
                                                    <div className="child-inwrap">
                                                        <img src={item1.url} alt="图片"/>
                                                        {!(this as any).state.loading ? <div className="close icon-custom-cuowu-01" onClick={this.del.bind(this, index, index1)}></div> : null}
                                                    </div>
                                                </div>
                                            );
                                        }
                                    })
                                }

                            </div>
                        );
                    })
                }   
                <input type="file" style={{display: "none"}} ref="file-input"/>
                {/* 加载动画 */}
                {(this as any).state.loading ? 
                <div className="loading-mask">
                    <embed src={loading_svg} type="image/svg-xml" className="loading"/>
                </div>: null}
            </div>
        );
    }
}