/**
 * 404 以及错误页面 
 */
import Error404 from "../pages/404/index";

/**
 * 主页面以及频道页面 
 */
import Init from "../pages/init/index";
import Home from "../pages/init/pages/home/index";
import Order from "../pages/init/pages/order/index";
import Person from "../pages/init/pages/person/index";

/**
 * 其余页面 
 */
import {Test} from "../pages/test/index";                           //测试
import TestModal from "../pages/test/modal";                //测试模态框
import Message from "../pages/message/index";               //消息页面
import Feedback from "../pages/feedback";                   //意见反馈页面
import About from "../pages/about";                         //关于页面
import Info from "../pages/info";                           //我的资料页面
import RefundCash from "../pages/refundCash";               //押金退还页面
import Wallet from "../pages/wallet";                       //钱包页面
import TransactionList from "../pages/transactionList";     //交易明细页面
import Identify from "../pages/identify";                   //身份认证
import RealName from "../pages/identify/realName";          //实名认证信息填写提交页面
import Recharge from "../pages/recharge";                   //充值页面
import CardOrTicket from "../pages/cardOrTicket";           //卡券页面
import EditNickname from "../pages/editNickname";           //编辑昵称页面
import EditPhone from "../pages/editPhone";                 //编辑手机页面
import Address from "../pages/address";                     //我的地址页面
import AddOrEditAddress from "../pages/addOrEditAddress";   //添加、编辑地址页面

export default [
    {
        path: '(\/|\/order|\/person)', // !!! 这里要做路由优先拦截
        component: Init,
        exact: true,
        children: [
            {
                title: "腾跃物联",
                path: '/',
                exact: true,
                component: Home 
            }, 
            {
                title: "我的订单",
                path: "/order",
                exact: true,
                component:  Order
            },
            {
                title: "我的",
                path: '/person',
                component: Person,
                exact: true,
                children: []
            }
        ]
    },
    {
        title: "消息",
        path: '/message',
        component: Message,
        exact: true,
        children: []
    },
    {
        title: "关于我们",
        path: '/personAbout',
        component: About,
        exact: true,
        children: []
    },
    {
        title: "意见反馈",
        path: '/personFeedback',
        component: Feedback,
        exact: false,
        children: []
    },
    {
        title: "个人资料",
        path: '/personInfo',
        component: Info,
        exact: false,
        children: []
    },
    {
        title: "押金退还",
        path: '/personRefundCash',
        component: RefundCash,
        exact: false,
        children: []
    },
    {
        title: "我的钱包",
        path: "/wallet",
        component: Wallet,
        exact: false,
        children: []
    },
    {
        title: "实名认证",
        path:'/personIdentify',
        component: Identify,
        exact: false,
        children: []
    },
    {
        title: "交易明细",
        path: "/TransactionList",
        component: TransactionList,
        exact: false,
        children: []
    },
    {
        title: "充值",
        path:"/personRecharge",
        component: Recharge,
        exact: false,
        children: []
    },
    {
        title: "开发测试",
        path: '/test',
        component: Test,
        exact: false,
        children: []
    },
    {
        title: "模态框测试",
        path:"/testModal",
        component: TestModal,
        exact: false,
        children: []
    },
    {
        title: "实名认证信息填写",
        path:"/realName",
        component: RealName,
        exact: false,
        children: []
    },
    {
        title: "优惠卡券",
        path: "/cardOrTicket",
        component: CardOrTicket,
        exact: false,
        children: []
    },
    {
        title: "修改昵称",
        path: "/editNickname",
        component: EditNickname,
        exact: false,
        children: []
    },
    {
        title: "修改手机",
        path: "/editPhone",
        component: EditPhone,
        exact: false,
        children: []
    },
    {
        title: "我的地址",
        path: "/address",
        component: Address,
        exact: false,
        children: []
    },
    {
        title: "新增地址",
        path: "/addOrEditAddress",
        component: AddOrEditAddress,
        exact: false,
        children: []
    },
    {
        title: "404",
        path: "*",
        component: Error404
    }
]