import React, {Component} from 'react';
import { List, InputItem, Picker } from 'antd-mobile';
import { district } from 'antd-mobile-demo-data';
import "./less/realName"

import Topbar from "../../components/topbar/index";

export default class RealName extends Component {
    constructor(props){
        super(props);
        this.state = {
        }
    }
    render () {
        return (
            <div className="real_name_content">
                <div className="top-wrap">
                    <Topbar title="实名认证信息填写"/>
                </div>
                <div className="content-wrap">

                    <div className="description_text">
                        为遵守国家《互联网安全使用条例》，同时保障用户安全，我 们为每一位用户购置了人身保险，需要您配合完成实名认证。
                    </div>
                    <List renderHeader={() => '实名认证'} renderFooter={() => '*我们将对您的隐私信息严格保护，请放心填写。'}>
                        <InputItem
                            clear
                            placeholder="请输入您的姓名"
            
                        >*真实姓名:</InputItem>
                        <InputItem
                            clear
                            placeholder="请输入您的身份证号"
            
                        >*身份证号:</InputItem>
                    </List>

                    <List className="marTop12" renderHeader={() => '紧急联系人'} renderFooter={() => '*为了保障您的安全，需要填写一个紧急联系人。'}>
                        <InputItem
                            clear
                            placeholder="请输入联系人姓名"
            
                        >*姓名：</InputItem>
                        <InputItem
                            clear
                            placeholder="请输入联系人手机号码"
            
                        >*手机号码：</InputItem>
                    </List>

                    <List className="marTop12" renderHeader={() => '常驻地址'}>
                        <Picker extra="请选择省市区"
                            data={district}
                            title="请选择省市区"
                            >
                            <List.Item arrow="horizontal">所在地区：</List.Item>
                        </Picker>
                        <InputItem
                            clear
                            placeholder="请输入小区/单位/门牌号等"
            
                        >详细地址:</InputItem>
                    </List>

                    {/* 提交按钮 */}
                    <div className="btn">提交</div>
                    
                </div>
            </div>
        )
    }

}