import React, {Component} from "react";
import { List } from 'antd-mobile';
import { Button, WhiteSpace,Toast} from 'antd-mobile';
import LoadImg from "../../../../components/myImg";
import "./less/index"
import Topbar from "../../../../components/topbar/index";
import MessageIcon from "../../../../components/topbar/components/messageIcon/index";
const Item = List.Item;

/**
 * 固定icon图片引入 
 */
import wallet from "./icon/wallet.png";
import guarantee_deposit from "./icon/guarantee_deposit.png";
import voucher from "./icon/voucher.png";
import service_color from "./icon/service_color.png";
import recharge from "./icon/recharge.png";
import insurance from "./icon/insurance.png";
import suggestion from "./icon/suggestion.png";
import aboutus from "./icon/aboutus.png";
import more from "./icon/more.png";
import store from "../../../../store";

export default class Person extends Component {
    state = {
        profile: {
            avatar:"",
            gender:255,
            genderText:"",
            nickname:""
        }
    }

    componentDidMount () {
        console.log(store.getState().user)
        this.setState({profile: store.getState().user.profile});
    }

    /**
     * 因为布局有的地方不方便使用Link
     * 所以用函数执行跳转 
     */
    toPage (path) {
        if (!path) return;
        (this as any).props.history.push(path);
    }

    logout () {
        Toast.info("退出登录逻辑");
    }

    render () {
        const state = this.state;
        return (
            <div className="person_content">

                {/* 头部 */}
                <div className="header_wrapper pad12">
                    <div className="top-wrap">
                        <Topbar title="我的" background="none" leftChild={<MessageIcon num={2}/>}/>
                    </div>
                    <div className="avatar" onClick={this.toPage.bind(this, "/personInfo")}>
                        <LoadImg src={state.profile.avatar} ></LoadImg>
                    </div>
                    <div>
                        <div className="mill_name">{state.profile.nickname || "暂无昵称"}</div>
                        <div className="tags">
                            <div className="attestation">已认证</div>
                            <div className="warrant">已担保</div>
                        </div>
                    </div>
                </div>
                
                {/* 菜单栏 */}
                <div className="menu-wrap">
                    <div className="item" onClick={this.toPage.bind(this, "/personRefundCash")}>
                        <div className="img-wrap">
                            <img src={guarantee_deposit} style={{height: " 44px",  width: "43px"}}/>
                        </div>
                        <div className="text">押金管理</div>
                    </div>
                    <div className="item" onClick={this.toPage.bind(this, "/wallet")}>
                        <div className="img-wrap">
                            <img src={wallet} style={{height: "40px", width: "36px"}}/>
                        </div>
                        <div className="text">我的钱包</div>
                    </div>
                    <div className="item" onClick={this.toPage.bind(this, "/cardOrTicket")}>
                        <div className="img-wrap">
                            <img src={voucher} style={{height: "41px", width: "42px"}}/>
                        </div>
                        <div className="text">优惠卡券</div>
                    </div>
                    <div className="item">
                        <div className='img-wrap'>
                            <img src={service_color} style={{width: "36px", height: "40px"}}/>
                        </div>
                        <div className="text">联系客服</div>
                    </div>
                </div>

                {/* 列表 */}
                <div className="menu-list-wrap">
                    <div className="menu-list">
                        <div className="item" onClick={this.toPage.bind(this, "/personRecharge")}>
                            <div className="left">
                                <img src={recharge} alt=""/>
                            </div>
                            <div className="center">余额充值</div>
                            <div className="blance">30.00</div>
                            <img className="right" src={more} alt=""/>
                        </div>
                        <div className="item" onClick={this.toPage.bind(this, "/personIdentify")}>
                            <div className="left">
                                <img src={insurance} alt=""/>
                            </div>
                            <div className="center">身份认证</div>
                            <img className="right" src={more} alt=""/>
                        </div>
                        <div className="item" onClick={this.toPage.bind(this, "/personFeedBack")}>
                            <div className="left">
                                <img src={suggestion} alt=""/>
                            </div>
                            <div className="center">意见反馈</div>
                            <img className="right" src={more} alt=""/>
                        </div>
                        <div className="item" onClick={this.toPage.bind(this, "/personAbout")}>
                            <div className="left">
                                <img src={aboutus} alt=""/>
                            </div>
                            <div className="center">关于我们</div>
                            <img className="right" src={more} alt=""/>
                        </div>
                    </div>
                </div>


                {/* 退出登录 在改版中去除了登录按钮，这里暂时保留，以免再用回来*/}
                {/* <div className="btn" onClick={(this as any).logout.bind(this)}>退出登录</div> */}


            </div>
        );
    }
}