import React from "react";
import "./less/index.less";


interface Props {
    title?: string,
    tips?: string,
    topSpacing?: number
}

export default class Pullup extends React.Component {
    static defaultProps: Props = {
        title: "缺省标题",
        tips: "缺省tips",
        topSpacing: 50
    }
    constructor (props) {
        super(props);
        this.state.minTop = (this as any).props.topSpacing;
    }
    state = {
        height: "100%",
        top: 0,
        maxTop: 0,
        minTop: 0,
        transition: false,
        closeIcon: false
    }
    componentDidMount () {
        this.disableContentMove();
        this.initMove();
        const height = (this as any).refs.pullup.offsetHeight, wrapHeight = height - this.state.minTop;
        this.setState({top: height - 50, maxTop: height - 50, height: wrapHeight + "px"});
    }
    initMove () {
        let move = 0, start = 0;
        (this as any).refs.pullup.addEventListener("touchstart", ({changedTouches:[{screenY: y}]}) => {
            move = y;
            start = y;
        });
        (this as any).refs.pullup.addEventListener("touchmove", e => {
            e.preventDefault();
            const 
            {changedTouches:[{screenY: y}]} = e,
            spacing = y - move;
            move = y;
            if (
                this.state.top + spacing > this.state.maxTop ||
                this.state.top + spacing <= this.state.minTop
            ) return;
            this.state.top += spacing;
            this.setState({});
        });
        (this as any).refs.pullup.addEventListener("touchend", ({changedTouches:[{screenY: y}]}) => {
            move = 0;
            const moveSpacing = y - start;
            if (moveSpacing > 0 ) {
                if (Math.abs(moveSpacing) > (this.state.maxTop / 3)) {
                    this.setTransition();
                    this.setState({top: this.state.maxTop, closeIcon: false});
                } else {
                    this.setTransition();
                    this.setState({top: this.state.minTop, closeIcon: true});
                }
            }
            if (moveSpacing < 0) {
                if (Math.abs(moveSpacing) > (this.state.maxTop / 3)) {
                    this.setTransition();
                    this.setState({top: this.state.minTop, closeIcon: true});
                } else {
                    this.setTransition();
                    this.setState({top: this.state.maxTop, closeIcon: false});
                }
            }
        });
    }

    disableContentMove () {
        (this as any).refs["content-wrap"].addEventListener("touchstart", e => {
            e.stopPropagation();
        });
        (this as any).refs["content-wrap"].addEventListener("touchmove", e => {
            e.stopPropagation();
        });
        (this as any).refs["content-wrap"].addEventListener("touchend", e => {
            e.stopPropagation();
        });
    }

    setTransition () {
        this.state.transition = true;
        this.setState({});
        setTimeout(() => {
            this.setState({transition: false});
        }, 300);
    }

    close () {
        this.setTransition();
        this.setState({top: this.state.maxTop, closeIcon: false});
    }

    render (): any {
        return (
            <div className="pullup" ref="pullup" 
            style={{
                height: this.state.height,
                top: this.state.top, 
                transition: this.state.transition ? "all .3s" : ""
            }}
            >
                <div className="top-wrap">
                    <div className="up-icon"></div>
                    {this.state.closeIcon ? <div className="title">{(this as any).props.title}</div> : <div className="up-icon-text">{(this as any).props.tips}</div>}
                    {this.state.closeIcon ? <div className="close-icon icon-custom-cha" onClick={this.close.bind(this)}></div> : null}
                </div>
                <div className="content-wrap" ref="content-wrap">
                    {this.props.children}
                </div>
            </div>
        )
    }
}