import React from "react";
import "./less/index.less";

import Topbar from "../../components/topbar/index";

/**
 * 图标资源 
 */
import icon from "./icon/empty.png";

export default class CardOrTicket extends React.Component {
    constructor (props) {
        super(props);
    }

    render (): any {
        return (
            <div className="cardorticket-wrap">
                <div className="top-wrap">
                    <Topbar title="优惠卡券"/>
                </div>
                <div className="content-wrap">
                    <div className="none-icon">
                        <img src={icon} alt=""/>
                        <div className="text">目前还什么都没有~</div>
                    </div>
                </div>
            </div>
        );
    }
}