import React from "react";
import "./less/index.less";

import Topbar from "../../components/topbar/index";

/**
 * 图片资源 
 */
import calendar from "./icon/calendar.png";
import pulldown from "./icon/pulldown.png";

export default class TransactionList extends React.Component {
    constructor (props) {
        super(props);
    }

    state = {

        //筛选部分状态数据
        filter: {
            listHeight: 0,
            checked: {text: "全部交易", id: 1, checked: true},
            list: [
                {text: "全部交易", id: 1, checked: true},
                {text: "租电支付", id: 2, checked: false},
                {text: "余额充值", id: 3, checked: false}
            ]
        }
    }
    
    componentDidMount () {
    }

    //筛选收起与展开
    toggleFilterList () {
        const filter: any = this.state.filter;
        filter.listHeight === 0 ? (filter.listHeight = filter.list.length * 50 + "px") : (filter.listHeight = 0);
        this.setState({});
    }

    //筛选选择点击
    filterCheck (item) {
        this.state.filter.list.forEach(item => item.checked = false);
        item.checked = true;
        this.state.filter.checked = item;
        this.toggleFilterList();
    }

    render (): any {
        return (
            <div className="transactionList-wrap">
                <div className="top-wrap">
                    <Topbar title="交易明细"/>
                </div>
                <div className="content-wrap">

                    {/* 筛选 */}
                    <div className="filter-bar" style={{height: this.state.filter.listHeight === 0 ? "50px" : "100%"}}>
                        <div className="filter-inwrap">
                            <div className="left" onClick={this.toggleFilterList.bind(this)}>{this.state.filter.checked.text} <img className={"left-icon" + (this.state.filter.listHeight === 0 ? " left-icon_down" : "")} src={pulldown} alt=""/></div>
                            <img className="right" src={calendar} alt=""/>
                        </div>
                        {/* 展开的列表 */}
                        <div className="filter-list" style={{height: this.state.filter.listHeight}}>
                            {
                            this.state.filter.list.map(item => {
                                return (
                                    <div className={"row" + (item.checked ? " row_active" : "")} onClick={this.filterCheck.bind(this, item)}>{item.text}</div>
                                )
                            })
                            }
                        </div>
                    </div>
                    <div className="filter-bar-spacing"></div>

                    <div className="row-list">
                        <div className="month-row">11月</div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>

                        <div className="month-row">12月</div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>
                        <div className="row">
                            <div className="top">
                                <div className="left">租电支付</div>
                                <div className="right">-13.00</div>
                            </div>
                            <div className="bottom">2019-12-09 13:09:37</div>
                        </div>

                    </div>

                </div>
            </div>
        )
    }
}