import React from "react";
import "./less/index.less";
import {withRouter} from "react-router-dom";

import icon from "./icon/news.png";

import store from "../../../../store/index";

class MessageIcon extends React.Component {
    static defaultProps = {
        num: 0,
        //该属性有则执行，没有则跳转到message页面
        onClick: null
    }
    state = {
        num: 0,
    }
    constructor (props:any) {
        super(props);
    }

    componentDidMount () {
        this.getMessage();
        store.subscribe(this.getMessage.bind(this));
    }

    getMessage () {
        this.setState({num: store.getState().message});
    }

    touch () {
        if ((this as any).props.onClick) {
            (this as any).props.onClick();
        } else {
            (this as any).props.history.push("/message");
        }
    }

    render ():any {
        const state = this.state;
        return (
            <div className="message-icon"
            onClick={(this as any).touch.bind((this as any))}
            >
                <img src={icon} alt=""/>
                {state.num ? <div className="pointer">{state.num}</div> : ""}
            </div>
        );
    }
}

export default withRouter(MessageIcon);