import React from "react";
import Modal from "../../components/modal/index";
export default class Home extends React.Component {
    constructor (props: any) {
        super(props);
        this.state = {
            //地图对象 
            map: null
        }

    }
    componentDidMount () {
        console.log(_ENV_)
    }
    handleConfirm = () => {
        (this as any).refs.modal.setState({
            mask_show: false
        });  
        setTimeout(()=>{
            (this as any).refs.modal.setState({
                delay_mask_show: false
            })  
        },500);
    }
    handleCancel = () => {
        alert(3)
    }
    render (): any {
        return (
            <div>
                {/* 模态窗 */}
                <Modal ref="modal" 
                    onConfirm={(this as any).handleConfirm} 
                    onCancel={(this as any).handleCancel}
                    //类型：alert,confirm,loading,slot
                    type={"alert"}
                    //类型success,error
                    status={"success"}
                    //标题
                    title={"标题"}
                    //图片名checking,empty,fail,success
                    img_name={"fail"}
                    //状态文字
                    text={"状态文字"}
                    //描述文字
                    description={"描述文字"}
                    //确定按钮
                    confirm_text={"确定"}
                    cancel_text={"取消"}
                >
                </Modal>
            </div>
        );
    }
}