import React from "react";
import {Route, Switch, Redirect, withRouter} from "react-router-dom";

class MyRouter extends React.Component {
    static defaultProps = {
        //路由列表
        routes: []
    }
    constructor (props) {
        super(props);
        this.setPageTitle((this as any).props.location.pathname);
        (this as any).props.history.listen(({pathname}) => this.setPageTitle(pathname));
    }

    /**
     * 设置document标题 
     * 
     * @param {String} pathname
     */
    setPageTitle (pathname): void {
        if (!pathname) return;
        const routesItem = (this as any).props.routes.find(item => item.path.toLowerCase() === pathname.toLowerCase());
        if (!routesItem || typeof routesItem.title === "undefined") return;
        if (document.title !== routesItem.title) document.title = routesItem.title;
    }

    render (): any {
        return (
            <Switch>
                {
                    (this as any).props.routes.map(item => {
                        //可以在这里进行权限拦截
                        if (item.redirect) {
                            return <Redirect exact={item.exact} from={item.path} to={item.redirect}/>
                        } else {
                            return <Route path={item.path} exact={item.exact} render={props => <item.component {...props} {...item}/>}/>;
                        }
                    })
                }
            </Switch>
        );
    }
}

export default withRouter(MyRouter);