import React from "react";
import "./less/index.less";

import Topbar from "../../components/topbar/index";
import {Toast} from "antd-mobile";

export default class Address extends React.Component {

    state = {
        addressList: [
            {
                isDefault: true,
                title: "江苏省无锡市新吴区长江路",
                address: "无锡新创科技创业园B栋8405号（星巴克楼上4楼出电梯口第一家就是）",
                contact: "收件人：腾跃科技  15266668888"
            },
            {
                isDefault: false,
                title: "江苏省无锡市新吴区长江路江苏省无锡市新吴区长江路",
                address: "无锡新创科技创业园B栋8405号",
                contact: "收件人：头脑峰会  15266668888"
            }
        ],

        //长按弹出菜单相关状态
        longTouchMenu: {
            index: -1,
            top: 0,
            left: 0,
            display: "none",
        }
    }

    constructor (props) {
        super(props);
    }


    /**
     * 长按事件监听 
     */
    longTouchStart (index, {changedTouches: [{clientX: x, clientY: y}]}) {
        y -= 50; //当前y是client为准的，所以要减去topbar高度
        x -= 125; //右边贴住手按住的x坐标
        const 
        item: any = this.state.addressList[index],
        card: any = this.refs["card-" + index];
        item.timer = setTimeout(() => {
            (this as any).openLongTouchMenu(index, x, y);
            clearTimeout(item.timer);
        }, 300);
    }
    longTouchEnd (index) {
        const item: any = this.state.addressList[index];
        clearTimeout(item.timer);
    }

    /**
     * 打开、关闭 长按菜单
     */
    openLongTouchMenu (index: number, left: number = 0, top: number = 0) {
        if (typeof Number(index) !== "number") return;
        const menuState: any = this.state.longTouchMenu;
        menuState.index = index;
        menuState.top = top;
        menuState.left = left;
        menuState.display = "block";
        this.setState({});
    }
    offLongTouchMenu () {
        const menuState: any = this.state.longTouchMenu;
        menuState.index = -1;
        menuState.top = 0;
        menuState.left = 0;
        menuState.display = "none";
        this.setState({});
    }

    /**
     * 长按菜单按钮回掉 
     */
    longTouchMenuEdit () {
        const index = this.state.longTouchMenu.index;
        // Toast.info(`编辑${index}逻辑`);
        this.toPage("/addOrEditAddress?type=1");
    }
    longTouchMenuDel () {
        const index = this.state.longTouchMenu.index;
        Toast.info(`删除${index}逻辑`);
    }
    longTouchMenuDefault () {
        const index = this.state.longTouchMenu.index;
        Toast.info(`设为默认${index}逻辑`);
    }

    /**
     * 跳转页面 
     */
    toPage (path) {
        if (!path) return;
        (this as any).props.history.push(path);
    }

    render (): any {
        const state: any = this.state;
        return (
            <div className="address-wrap">
                <div className="top-wrap">
                    <Topbar title="我的地址"/>
                </div>
                <div className="content-wrap">

                    {/* 长安菜单 */}
                    <div className="longtouchmenu" 
                    style={{display: state.longTouchMenu.display}}
                    onClick={this.offLongTouchMenu.bind(this)}
                    >
                        <div className="menu" style={{
                            top: state.longTouchMenu.top,
                            left: state.longTouchMenu.left
                        }}>
                            <div className="btn" onClick={this.longTouchMenuEdit.bind(this)}>编辑</div>
                            <div className="btn" onClick={this.longTouchMenuDel.bind(this)}>删除</div>
                            <div className="btn" onClick={this.longTouchMenuDefault.bind(this)}>设为默认</div>
                        </div>
                    </div>

                    {/* 地址列表 */}
                    {
                    state.addressList.map((item, index) => {
                        return (
                            <div className="address-card"
                            onTouchStart={this.longTouchStart.bind(this, index)} 
                            onTouchEnd={this.longTouchEnd.bind(this, index)}
                            >
                                <div className="row">
                                    {item.isDefault && <div className="title title_red">(默认)&nbsp;</div>}
                                    <div className="title">{item.title}</div>
                                    <div className="title title_icon icon-custom-xiugainicheng1" style={{color: "#606060"}} onClick={this.toPage.bind(this, "/addOrEditAddress?type=1")}></div>
                                </div>
                                <div className="row">
                                <div className="text1">{item.address}</div>
                                </div>
                                <div className="row">
                                <div className="text2">{item.contact}</div>
                                </div>
                            </div>
                        );
                    })
                    }
                    
                    {/* 新建按钮 */}
                    <div className="add-btn" onClick={this.toPage.bind(this, "/addOrEditAddress")}>新增收货地址</div>

                </div>
            </div>
        );
    }
}