import React, {Component} from "react";

import "./less/about"
import { List } from "antd-mobile";
const Item = List.Item;
import Topbar from "../../components/topbar/index";

export default class About extends Component {

    render () {
        return (
            <div className="about_content">
                <div className="top-wrap">
                    <Topbar background="none" title="关于我们"/>
                </div>
                {/* 头部 */}
                <div className="header_wrapper pad12">
                    <div className="avatar"></div>
                    <div className="name">腾跃共享电池</div>
                    <div className="desc">共享科技，共享美好生活!</div>
                </div>
                 {/* 列表 */}
                 <List className="list_wrapper">
                    <Item
                        arrow="horizontal"
                        extra="腾跃科技"
                        onClick={() => {}}
                        >微信服务号
                    </Item>
                    <Item
                        onClick={() => {(this as any).props.history.push('/personFeedback')}}
                        arrow="horizontal"
                        extra="www.tengyueiot.com"
                    >
                        官方网站
                    </Item>
                    <Item
                        onClick={() => {(this as any).props.history.push('/personAbout')}}
                        arrow="horizontal"
                        extra="0510-6666-8888"
                    >
                        联系电话
                    </Item>
                    <Item
                        onClick={() => {(this as any).props.history.push('/personAbout')}}
                        arrow="horizontal"
                        extra="tengyuetech.163.com"
                    >
                        电子邮箱
                    </Item>
                </List>
                {/* 公司名 */}
                <div className="company">无锡腾跃科技有限公司</div>
            </div>
        );
    }
}    