import React from "react";
import "./less/index.less";

import {Switch, Picker} from "antd-mobile";
import { district, provinceLite } from 'antd-mobile-demo-data';
import Topbar from "../../components/topbar";

/**
 * 静态图片资源 
 */
import more from "./icon/more.png";

export default class AddOrEditAddress extends React.Component {

    state = {
        title: "",
        isDefault: false
    }

    constructor (props) {
        super(props);
    }

    componentDidMount () {

        {
            //根据具体的场景来设置
            const search: any = new URLSearchParams((this as any).props.location.search);
            if (!search.get("type")) {
                this.setTitle("增加地址");
            } 
            if (Number(search.get("type")) === 1) {
                this.setTitle("编辑地址");
            }
        }
    }

    setTitle (title = "") {
        this.setState({title});
        document.title = title;
    }

    render (): any {
        const state: any = this.state;
        return (
            <div className="addoreditaddress-wrap">
                <div className="top-wrap">
                    <Topbar title={state.title}/>
                </div>
                <div className="content-wrap">

                    <Picker 
                    extra="请选择(可选)"
                    data={district}
                    title="城市选择"
                    onOk={e => console.log('ok', e)}
                    // onDismiss={e => console.log('dismiss', e)}
                    >
                    <div className="input-row">
                        <div className="label">所在省市区：</div>
                        <input type="text" placeholder="江苏省 无锡市 锡山区 "/>
                        <img className="right-icon" src={more} alt=""/>
                    </div>
                    </Picker>

                    <div className="input-row">
                        <div className="label">所在街道（乡镇）：</div>
                        <input type="text" placeholder="新明路"/>
                        {/* <img className="right-icon" src={more} alt=""/> */}
                    </div>

                    <div className="input-row input-row_noflex">
                        <div className="label">详细地址：</div>
                        <textarea placeholder="无锡新创科技创业园B栋8405号（星巴克楼上4楼出电梯口第一家就是）"></textarea>
                        {/* <div className="right-icon"></div> */}
                    </div>
                    <div className="input-row">
                        <div className="label">收件人：</div>
                        <input type="text" placeholder="腾跃科技研发部"/>
                        {/* <img className="right-icon" src={more} alt=""/> */}
                    </div>
                    <div className="input-row">
                        <div className="label">收件电话：</div>
                        <input type="text" placeholder="1566668889"/>
                        {/* <img className="right-icon" src={more} alt=""/> */}
                    </div>
                    <div className="input-row">
                        <div className="label">设为默认地址：</div>
                        {/* <div className="right-switch"></div> */}
                        <Switch
                        checked={this.state.isDefault}
                        onChange={() => {
                            this.setState({
                                isDefault: !this.state.isDefault,
                            });
                        }}
                        />
                    </div>

                    {/* 按钮 */}
                    <div className="save-btn">修改并保存</div>
                </div>
            </div>
        );
    }
}