import React, {Component} from "react";

import "./less/index"
import { List } from "antd-mobile";
const Item = List.Item;
import Topbar from "../../components/topbar/index";

export default class Info extends Component {

    toPage (path): void {
        (this as any).props.history.push(path);
    }

    render () {
        return (
            <div className="info_content">
                <Topbar title="我的资料"/>
                 {/* 头部 */}
                <div className="header_wrapper pad12">
                    <div className="avatar"></div>
                    <div className="name">更换头像</div>
                </div>
                 {/* 列表 */}
                 <List className="list_wrapper">
                    <Item
                        arrow="horizontal"
                        extra="腾跃科技"
                        onClick={this.toPage.bind(this, "/editNickname")}
                        >昵称
                    </Item>
                    <Item
                        onClick={() => {}}
                        arrow="horizontal"
                        extra="未设置"
                    >
                        性别
                    </Item>
                    <Item
                        onClick={() => {}}
                        arrow="horizontal"
                        extra="1998.10.01"
                    >
                        生日
                    </Item>
                    <Item
                        onClick={this.toPage.bind(this, "/editPhone")}
                        arrow="horizontal"
                        extra="158****8888"
                    >
                        手机号码
                    </Item>
                    <Item
                        onClick={this.toPage.bind(this, "/address")}
                        arrow="horizontal"
                    >
                        我的地址
                    </Item>
                </List>
            </div>
        );
    }
}    